@keyframes header-visible-animate {
  from {
    max-height: 0;
  }
  to {
    max-height: 200px;
  }
}

@keyframes option-enter-animate {
  to {
    right: 0;
  }
}

@keyframes option-enter-animate-lg {
  to {
    right: 20px;
  }
}

